.post-feed {
  font-size: 0.9rem;
  margin: 0 0 20px 0;
  color: #333333; }
  .post-feed header {
    border-bottom: 1px solid #f1f2f3; }
    .post-feed header .title {
      font-size: 1.2rem; }
    .post-feed header .category {
      font-size: 0.9422rem; }
  .post-feed figure.cover {
    position: relative; }
    .post-feed figure.cover .media {
      position: relative;
      display: block;
      width: 100%;
      max-height: 600px;
      overflow: hidden;
      object-fit: cover; }
      .post-feed figure.cover .media img {
        object-fit: cover;
        width: 100%; }
  .post-feed .meta {
    display: -ms-flexbox;
    display: flex;
    font-size: 0.9rem; }
    .post-feed .meta .author {
      margin: 0 10px; }
    .post-feed .meta .comments {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-line-pack: center;
          align-content: center;
      -ms-flex-pack: center;
          justify-content: center;
      margin-left: auto; }
  .post-feed summary .read_more {
    float: right; }

